import React, { useState, useEffect } from "react";
import { Doughnut } from "@reactchartjs/react-chart.js";
import {
  URGENCY_LEVEL_MEDIUM,
  URGENCY_LEVEL_HIGH,
  URGENCY_LEVEL_LOW,
} from "../../labels";

const DoughNutChart = (props) => {
  const [data, setData] = useState({
    labels: [URGENCY_LEVEL_HIGH, URGENCY_LEVEL_MEDIUM, URGENCY_LEVEL_LOW],
    datasets: [],
  });
  const options = {
    title: {
      display: true,
      text: props.chartTitle,
    },
    legend: {
      onClick: null,
    },
  };

  // options = {
  //   dataLabels: {
  //     enabled: true,
  //     formatter: function (val) {
  //       return val + "%"
  //     },
  //     dropShadow: {
  //       ...
  //     }
  //   }
  // }

  useEffect(() => {
    const urgencies = [
      props.urgencyLevels.high,
      props.urgencyLevels.medium,
      props.urgencyLevels.low,
    ];
    let set = [
      {
        label: "",
        data: urgencies,
        backgroundColor: [
          "rgba(234, 80, 55)",
          "rgba(32, 50, 92)",
          "rgba(194, 194, 214)",
        ],
        borderColor: [
          "rgba(255, 255, 255)",
          "rgba(255, 255, 255)",
          "rgba(255, 255, 255)",
        ],
        borderWidth: 3,
      },
    ];
    const finalData = {
      ...data,
      datasets: set,
    };
    setData(finalData, data);
  }, [props.urgencyLevels]);

  return (
    <>
      <Doughnut id="doughNutChart" data={data} options={options} />
    </>
  );
};

export default DoughNutChart;
