import React, { Component } from "react";
import { Modal, Form, Col, DatePicker, Alert } from "antd";
import FloatLabel from "../form/FloatLabels";
import moment from "moment";
import "../modal/modal.scss";
import "date-fns";
import { TimePicker } from "@progress/kendo-react-dateinputs";
import {
  CANCEL,
  EXTEND_END_DATE,
  EXTEND_TEXT_MODAL,
  POLL,
} from "../../headings";
import {
  EXTEND_END_DATE_TEXT_PART1,
  EXTEND_END_DATE_TEXT_PART2,
} from "../../messages";
import momentUtil from "../../utils/momentUtil";
import { SELECT_END_DATE, SELECT_END_TIME } from "../../labels";
import { SELECT_DATE, PAST_TIME } from "../../errors";

class ExtendEndDate extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false,
      endDate: "",
      error: false,
      endTime: new Date(),
      end_time: new Date(),
      endTimeError: false,
    };
  }

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  componentDidMount() {
    let { end_date, end_time, timezone } = this.props.pollsData;

    // const endDateGet = end_date ? moment(end_date).format("YYYY-MM-DD") : moment().format("YYYY-MM-DD");
    // const endTimeGet = end_time && end_time.includes("Nan") ? end_time : moment().format("HH:mm:ss");
    // const formatedDateTimeToPass = endDateGet + "T" + endTimeGet + "Z";
    // const convetUTCTimeTOLocalTime = moment(formatedDateTimeToPass).tz(timezone).format("YYYY-MM-DD HH:mm:ss")
    // const usedDateWithFormat = new Date(convetUTCTimeTOLocalTime);


    const timeZoneData = timezone ? timezone : "Asia/Kolkata"
    const end_DateTime1 = end_date ? moment(end_date).tz(timeZoneData).format("YYYY-MM-DDTHH:mm:ss") : moment().tz(timeZoneData).format("YYYY-MM-DDTHH:mm:ss");
    const endDateTime = new Date(end_DateTime1);

    // console.log("---endDateGet--", endDateGet, "---endTimeGet--", endTimeGet, "timezone--", timezone);

    // console.log("---formatedDateTimeToPass--", formatedDateTimeToPass);
    // console.log("---convetUTCTimeTOLocalTime--", convetUTCTimeTOLocalTime);
    // console.log("---usedDateWithFormat--", usedDateWithFormat);
    this.setState({
      endDate: moment(endDateTime, "DD-MM-YYYY"),
      endTime: endDateTime,
      end_time: endDateTime,
    });
  }

  handleOk = () => {
    if (
      !this.state.endDate ||
      this.state.endDate === null ||
      !this.state.endTime
    ) {
      this.setState({ error: true });
    } else {
      this.setState({ error: false, visible: false });
      let obj = {
        poll_id: this.props.pollsData.poll_id,
        pollName: this.props.pollsData.title,
      };
      this.props.extendPollDate(obj, this.state);
    }
  };

  handleCancel = () => {
    this.setState({
      visible: false,
    });
    window.location = "/orgcc/pollsdirectory";
  };

  onChangeEndDate = (date) => {
    let time = moment().tz(this.props.pollsData.timezone).format("YYYY-MM-DDTHH:mm:ss")

    this.setState({
      endDate: date,
      endTime: new Date(time)
    });
  };

  onChangeEndTime = (event) => {

    const endTime = event.target.value;
    const extractTime = moment(endTime).format("HH:mm");
    const selectedEndDate = this.state.endDate ? moment(this.state.endDate).format("YYYY-MM-DD") : moment().format("YYYY-MM-DD");
    const dateTimeClubed = selectedEndDate + "T" + extractTime;
    const actualendTime = new Date(dateTimeClubed);
    const currentTime = moment().tz(this.props.pollsData.timezone).format("YYYY-MM-DDTHH:mm");
    const dateBasedOnTimeZone = new Date(currentTime);
    if (actualendTime < dateBasedOnTimeZone) {
      //showError(true);
    } else {
      this.setState({ endTimeError: false, endTime: actualendTime });

    }
  };

  onBlurEndTime = (event) => {
    const currentTime = new Date();
    if (
      currentTime > this.state.endTime &&
      this.state.endTime.getTime() !== this.state.end_time.getTime()
    ) {
      this.setState({
        endTimeError: true,
        endTime: this.state.end_time,
      });
    } else {
      this.setState({ endTimeError: false });
    }
  };

  disabledDate = (current) => {
    return current && current < moment().startOf("day");
  };

  range = (start, end) => {
    const result = [];
    for (let i = start; i < end; i++) {
      result.push(i);
    }
    return result;
  };

  render() {
    return (
      <>
        <a onClick={this.showModal}>{EXTEND_END_DATE}</a>
        <Modal
          title={EXTEND_END_DATE}
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          centered
          width={520}
          okButtonProps={{
            children: "Custom OK",
          }}
          cancelButtonProps={{
            className: "ant-btn-secondary",
            children: "Custom cancel",
          }}
          okText={EXTEND_TEXT_MODAL}
          cancelText={CANCEL}
        >
          <Form className="input-md floating-label w-100">
            <Col span={24} className="gutter-row">
              {this.state.error && (
                <Form.Item>
                  <Alert message={SELECT_DATE} type="error" showIcon></Alert>
                </Form.Item>
              )}
              <div className="mb-4 subtext-md font-weight-md6">
                {EXTEND_END_DATE_TEXT_PART1}{" "}
                <span className="pr-1"> {EXTEND_END_DATE_TEXT_PART2}</span>
                {this.props.pollsData.title} {POLL}
              </div>

              <FloatLabel label={SELECT_END_DATE} value="eDate">
                <Form.Item
                  name="eDate"
                  rules={[
                    {
                      required: true,
                      message: SELECT_DATE,
                    },
                  ]}
                >
                  <DatePicker
                    defaultValue={this.state.endDate}
                    disabledDate={this.disabledDate}
                    onChange={(date) => this.onChangeEndDate(date)}
                    className="w-100"
                    name="eDate"
                    className="required"
                  />
                  {this.state.endTimeError && (
                    <Alert message={PAST_TIME} type="error" showIcon></Alert>
                  )}
                  <TimePicker
                    value={this.state.endTime}
                    onChange={this.onChangeEndTime}
                    onBlur={this.onBlurEndTime}
                  />
                </Form.Item>
              </FloatLabel>
            </Col>
          </Form>
        </Modal>
      </>
    );
  }
}

export default ExtendEndDate;
